.btn {
  position: relative;
  display: inline-block;
  padding: 17px 43px;
  font-family: $font-family-one;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: .2px;
  color: #3b3b3d;
  text-decoration: none;
  border: 2px solid #6ed1cd;
  outline: none;
  cursor: pointer;
  transition: opacity ease-in-out .2s;

  &:before {
    content: '';
    position: absolute;
    top: -2px;
    left: -11px;
    width: 13px;
    height: 2px;
    background: #6ed1cd;
  }

  &:after {
    content: '';
    position: absolute;
    bottom: -2px;
    right: -11px;
    width: 13px;
    height: 2px;
    background: #6ed1cd;
  }

}


/* Medium Devices, Desktops */
@media only screen and (max-width: 1024px) {

}

/* Medium Devices, Desktops */
@media only screen and (max-width: 992px) {

  .btn-link-arrow {

    &:after {
      top: 5px;
    }

    &_mt {
      margin-top: 22px;
    }

    &_ml {
      margin-left: 0;
    }

    &_footer {
      margin-top: 67px;
    }

  }

}

/* Small Devices, Tablets */
@media only screen and (max-width: 760px) {

  .btn-link-arrow {
    font-size: 18px;

    &_mt {
      margin-top: 22px;
    }

    &_ml {
      margin-left: 0;
    }

    &_footer {
      margin-top: 67px;
    }

  }

}

