.row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
}

.col-pl {
  padding-left: 50px;
}

/* Medium Devices, Desktops */
@media only screen and (max-width : 992px) {
  .col-pl {
    padding-left: 40px;
  }
}

/* Small Devices, Tablets */
@media only screen and (max-width : 760px) {
  .col-pl {
    padding-left: 0;
  }
}


