.content-b {
  width: 100%;
  height: 100vh;

  &_2 {
    background: #6ed1cd;
  }

  &_3 {
    background: #e0e6ee;
  }

  &_4 {
    background: #8033e0;
  }

  &__wrap {
    padding-top: 235px;
  }

  &__title {
    display: inline-block;
    margin: 0;
    position: relative;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 208px;
    text-transform: uppercase;
    letter-spacing: .2px;
    background-image: url("../images/cm/cm1.png");
    background-repeat: no-repeat;
    background-color: #3b3b3d;
    background-size: 100% 100%;
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;

    &_2 {
      background-image: url("../images/cm/cm2.png");
    }

    &_3 {
      background-image: url("../images/cm/cm3.png");
    }

    &_4 {
      background-image: url("../images/cm/cm4.png");
    }

  }

  &__description {
    margin: 0;
    min-height: 82px;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 36px;
    color: #6ed1cd;

    &_2 {
      color: #fff;
    }

    &_3 {
      color: #8033e0;
    }

    &_4 {
      color: #6ed1cd;
    }

  }

  &__description-sub {
    margin-top: 50px;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 16px;
    color: #3b3b3d;
  }

  &__link-line {
    position: relative;
    margin-top: 25px;
    display: inline-block;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 16px;
    color: #3b3b3d;
    text-transform: uppercase;
    text-decoration: none;

    & span {
      position: absolute;
      top: 8px;
      right: -410px;
      width: 400px;
      height: 2px;
      background: #6ed1cd;

      &:before {
        content: '';
        position: absolute;
        top: -9px;
        right: 14px;
        width: 2px;
        height: 12px;
        background: #6ed1cd;
        transform: rotate(-45deg);
      }

      &:after {
        content: '';
        position: absolute;
        top: -9px;
        right: 6px;
        width: 2px;
        height: 12px;
        background: #6ed1cd;
        transform: rotate(-45deg);
      }

    }

    &_2 span {
      background: #fff!important;

      &:before {
        background: #fff!important;
      }

      &:after {
        background: #fff!important;
      }

    }

    &_3 span {
      background: #8033e0!important;

      &:before {
        background: #8033e0!important;
      }

      &:after {
        background: #8033e0!important;
      }

    }

    &_3 span {
      background: #6ed1cd!important;

      &:before {
        background: #6ed1cd!important;
      }

      &:after {
        background: #6ed1cd!important;
      }

    }

  }

}

