.footer {
  width: 100%;
  height: 100vh;

  &__bg {
    width: 100%;
    height: 100%;
    background: url("../images/sea.jpg") no-repeat;
    background-size: cover;
    background-position: center center;
  }

}

.footer-content {
  padding: 153px 20px 0 116px;

  &__title {
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 61px;
    text-transform: uppercase;
    line-height: 80px;
    letter-spacing: .2px;
    color: #3b3b3d;
  }

  &__btn-wrap {
    margin: 30px 0 0 11px;
  }

}

.footer-contact-list {
  margin-top: 100px;
  padding: 0;
  list-style: none;

  &__item {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 14px;
    letter-spacing: .2px;
    color: #3b3b3d;
    text-decoration: none;
  }

}

