.menu-wrap {
  visibility: hidden;
  opacity: 0;
  position: fixed;
  z-index: 1;
  width: 100%;
  height: 100vh;
  background: #fff;

  &.toggled {
    visibility: visible;
    opacity: 1;
  }

  &__sub-title {
    position: absolute;
    left: -243px;
    bottom: 190px;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 208px;
    color: #3b3b3d;
    text-transform: uppercase;
    letter-spacing: .2px;
    transform: rotate(-90deg);
  }

}

.menu-nav {
  padding-top: 190px;
}

.menu-list {
   list-style: none;

  &__item {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }

  }

  &__link {
    position: relative;
    font-family: $font-family-one;
    font-weight: 300;
    font-size: 36px;
    color: #3b3b3d;
    letter-spacing: .2px;
    text-decoration: none;

    &_active {
      padding-left: 33px;

      &:before {
        content: '#';
        position: absolute;
        left: 0;
        font-size: 36px;
        color: #6ed1cd;
      }

    }

  }

}
