.header {
  position: fixed;
  z-index: 2;
  width: 100%;
}

.header-wrap {
  position: relative;
  display: flex;
  justify-content: space-between;
}

.header-logo-wrap {
  margin: 44px 0 0 30px;
}

.header-menu-btn-wrap {
  position: absolute;

  top: 27px;
  //left: 0;
  //right: 0;
  //margin: 0 auto;

  left: 50%;
  margin-left: -30px;

  width: 60px;
  text-align: center;
}

.header-contact-wrap {
  margin: 55px 66px 0 0;
}

.header-menu-btn {
  $g: &;

  display: inline-block;
  max-width: 60px;
  background: transparent;
  border: 0;
  outline: 0;
  line-height: 0.6;
  text-align: center;
  cursor: pointer;

  //&:hover {
  //
  //  #{$g}__ic-wrap {
  //
  //    &:before {
  //      transform: rotate(-45deg);
  //    }
  //
  //    &:after {
  //      transform: rotate(45deg);
  //    }
  //
  //  }
  //
  //}

  &.toggled {

    #{$g}__ic-wrap {

      &:before {
        transform: rotate(-45deg);
      }

      &:after {
        transform: rotate(45deg);
      }

    }

  }

  &__ic-wrap {
    position: relative;
    display: inline-block;
    height: 21px;
    width: 16px;

    &:before {
      content: '';
      position: absolute;
      width: 2px;
      height: 21px;
      background: #6ed1cd;
      transition: transform .2s ease-in-out;
    }

    &:after {
      content: '';
      position: absolute;
      width: 2px;
      height: 21px;
      background: #6ed1cd;
      transition: transform .2s ease-in-out;
    }

  }

  &__text {
    display: block;
    margin-top: 13px;
    font-family: $font-family-one;
    font-weight: 800;
    font-size: 14px;
    color: #0d0d0e;
    text-transform: lowercase;
  }

}

.header-contact {
  position: relative;
  display: inline-block;
  padding-bottom: 6px;
  font-family: $font-family-one;
  font-weight: 800;
  font-size: 14px;
  color: #0d0d0e;
  text-transform: lowercase;
  text-decoration: none;
  line-height: 0.6;
  border-bottom: 2px solid #6ed1cd;
}

